  .landing-container {
    background-image: 
    linear-gradient(to bottom, #2c29ef, 86%, transparent),
    url('./images/img2.png');
    /* background: linear-gradient(#000639, #2c29ef, #30acf3); */
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .landing-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
  }
  
  .landing-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .landing-btns {
    margin-top: 32px;
  }
  
  .landing-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .landing-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .landing-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .landing-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }